import React from 'react'
import {connect} from 'react-redux'
import store from '../../config/store'
import coin from './coin.gif'
import {SPRITE_SIZE} from '../../config/constants'
import './styles.css'

export class Coin extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            position: []
        }
    }

    componentDidMount(){
        this.setState({
            position: this.props.position
        })

        store.dispatch({
            type: 'REGISTER_COIN',
            payload: {
                position : this.props.position,
                id : this.props.id
            }
        }) 
    }  

    render(){

        var getCoinClass = ''
        if(this.props.active){
            getCoinClass = ''
        }else{
            getCoinClass = 'get_coin'
        }
        return (
            <div className={getCoinClass}
                style={{
                    position:'absolute',
                    top:this.state.position[1],
                    left:this.state.position[0],
                    background: `url('${coin}') no-repeat center center transparent`,
                    backgroundSize:'40%',
                    backgroundPosition: 'center center',
                    width:`${SPRITE_SIZE}px`,
                    height:`${SPRITE_SIZE}px`
                }}
            />
        )
    }
}

function mapStateToProps(state, ownProps){
    return{
        ...state.coins[ownProps.id]
    }
}

export default connect(mapStateToProps)(Coin)