const intialState = {
    isHurt: false,
    isGameOver: false,
    difficulty:30,
    isVictory:false,
    time:0
}

const worldReducer = (state = intialState, action) => {
    switch(action.type){
        case "UPDATE_TIME":
            return {
                ...state,
                time:action.payload.time
            }
        case "IS_HURT":
            return {
                ...state,
                isHurt:action.payload.isHurt
            }
        case "IS_GAME_OVER":
            return {
                ...state,
                isGameOver:action.payload.isGameOver
            }
        case "VICTORY":
            return {
                ...state,
                isVictory:action.payload.victory
            }
        default:
            return state
    }
}

export default worldReducer