import store from '../../config/store'
import Monster from '../monster'
import soundCoin from '../../data/sounds/coin.mp3'
import {SPRITE_SIZE, MAP_WIDTH, MAP_HEIGHT} from '../../config/constants'

export default function handleMovement(player){ 
    const audioCoin = new Audio(soundCoin) 
    
    function observeBoundaries(newPos){
        //SI ON EST DANS LA MAP ON RETOURNE TRUE SINON FALSE
        return (newPos[0] >= 0 && newPos[0] <= MAP_WIDTH-SPRITE_SIZE) &&
                (newPos[1] >= 0 && newPos[1] <= MAP_HEIGHT-SPRITE_SIZE)
    }

    function observeImpassable(newPos){
        //TEST DES OBSTACLES
        const tiles = store.getState().map.tiles
        const y = newPos[1] / SPRITE_SIZE
        const x = newPos[0] / SPRITE_SIZE
        const nextTile = tiles[y][x]
        return  nextTile < 5
    }

    //UPDATE DE LA POSITION
    function getNewPosition(oldPos, direction){
        switch(direction){
            case 'WEST':
                return [oldPos[0]-SPRITE_SIZE, oldPos[1]]
            case 'EAST':
                return [oldPos[0]+SPRITE_SIZE, oldPos[1]]
            case 'NORTH':
                return [oldPos[0], oldPos[1]-SPRITE_SIZE]
            case 'SOUTH':
                return [oldPos[0], oldPos[1]+SPRITE_SIZE]
            default:
        }        
    }

    //UPDATE DU SPRITE
    function getSpriteLocation(direction, walkIndex){
        switch (direction){
            case 'EAST' :
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*4}px`
            case 'SOUTH' :
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*0}px`
            case 'WEST' :
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*2}px`
            case 'NORTH' :
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*3}px`
            default:
        }
    }

    function getWalkIndex(){
        const walkIndex = store.getState().player.walkIndex
        return walkIndex >= 4 ? 0 : walkIndex + 1
    }

    function getWalkCount(){
        const walkCount = store.getState().player.walkCount
        return walkCount
    }

    function getLives(){
        const lives = store.getState().player.lives
        return lives
    }

    function getCoins(){
        const coins = store.getState().player.coins
        return coins
    }

    //DISPATCH DE LA NOUVELLE POSITION ET DIRECTION POUR ANIMATION DU PERSO AU STORE
    function dispatchMove(direction, newPos){
        const walkIndex = getWalkIndex()
        const walkCount = getWalkCount()
        const lives = getLives() 
        const coins = getCoins()
        store.dispatch({
            type: 'MOVE_PLAYER',
            payload: {
                position: newPos,
                direction,
                walkIndex,
                lives,
                coins,
                walkCount: walkCount+1,
                spriteLocation : getSpriteLocation(direction, walkIndex)
            }
        })
    }

    var timeoutID;
    var timeoutIDSleep;

    function startTimer() {
        timeoutID = window.setTimeout(goInactive, 4000);
        timeoutIDSleep = window.setTimeout(goSleep, 8000);
    }
     
    function resetTimer(e) {
        window.clearTimeout(timeoutID);
        window.clearTimeout(timeoutIDSleep);     
        goActive();
    }
     
    function goInactive() {
        store.dispatch({
            type: 'INACTIVITY',
            payload: {
                spriteLocation : '40px 40px'
            }
        })
    }

    function goSleep() {
        store.dispatch({
            type: 'SLEEP',
            payload: {
                spriteLocation : '80px 40px'
            }
        })
    }
     
    function goActive() {             
        startTimer();
    }

    //DISPATCH DE LA NOUVELLE PIECE GAGNEE
    function getCoin(coinId, coinPosition){
        audioCoin.play()
        const coins = getCoins() + 1
        //ON AJOUTE LA PIECE AU STORE DU PLAYER
        store.dispatch({
            type: 'UPDATE_COINS',
            payload: {
                coins
            }
        })
        //ON RETIRE LA PIECE DU STORE
        store.dispatch({
            type: 'REMOVE_COIN',
            payload: {
                id:coinId,
                position:coinPosition
            }
        })
    }

    //VICTOIRE !!!!!
    function getVictory(){
        store.dispatch({
            type: 'VICTORY',
            payload: {
                victory:true
            }
        })  
    }

    //AVANT DE FAIRE UN MOUVEMENT ON CHECK SI ON PEUT
    function attemptMove(direction){
        const oldPos = store.getState().player.position
        const newPos = getNewPosition(oldPos,direction)
        if(observeBoundaries(newPos) && observeImpassable(newPos)){
            dispatchMove(direction, newPos)
            //----------- SI ON ARRIVE SUR UNE PIECE
                //ON CONVERTI LES STORE STATE DES COINS EN TABLEAU...
                const coins = Object.keys(store.getState().coins).map(function(key) {
                    return store.getState().coins[key];
                });

                //...ON PARCOURS LE TABLEAU ET SI LA POSITION DU PLAYER EST CELLE D'UNE PIECE...
                coins.map((answer, i) => {  
                    if(answer.position[0] === newPos[0] && answer.position[1] === newPos[1]){
                        //ON GAGNE LA PIECE
                        getCoin(answer.id, answer.position)
                    }
                })

            //------------ SI ON ARRIVE SUR UN MONSTRE
                //ON CONVERTI LES STORE STATE DES MONSTERS EN TABLEAU...
                const monsters = Object.keys(store.getState().monsters).map(function(key) {
                    return store.getState().monsters[key];
                });

                console.log(Monster.state)

                //...ON PARCOURS LE TABLEAU ET SI LA POSITION DU PLAYER EST CELLE D'UN MONSTER...
                monsters.map((answer, i) => {  
                    if(answer.position[0] === newPos[0] && answer.position[1] === newPos[1]){
                        
                        //UPDATE DU STORE POUR ANIMATION PLAYER TOUCHE
                        store.dispatch({
                            type: 'IS_HURT',
                            payload: {
                                isHurt:true
                            }
                        }) 
                        setTimeout(() => {
                            //PUIS REINITIALISER APRES X SECONDES
                            store.dispatch({
                                type: 'IS_HURT',
                                payload: {
                                    isHurt:false
                                }
                            }) 
                        }, 300); 

                        //ON ENLEVE UNE VIE
                        const lives = store.getState().player.lives - 1
                        //ON DISPATCHE AU STORE AVEC LA VIE EN MOINS
                        store.dispatch({
                            type: 'UPDATE_LIVES',
                            payload: {
                                lives
                            }
                        })  
                    }
                })
            
            //----------- SI ON ARRIVE SUR LA VICTOIRE                
            console.log(store.getState().victory.position[0])                
            if(store.getState().victory.position[0] === newPos[0] && store.getState().victory.position[1] === newPos[1]){
                //ON GAGNE LA VICTOIRE
                getVictory()
            }
        }
    }

    //ECOUTE DE L'APPUI SUR UNE TOUCHE
    function handleKeyDown(e){
        e.preventDefault()
        switch(e.keyCode){
            case 37:
                return attemptMove('WEST')
            case 38:
                return attemptMove('NORTH')
            case 39:
                return attemptMove('EAST')
            case 40:
                return attemptMove('SOUTH')
            default:
                // console.log(e.keyCode)
        }
    }

    window.addEventListener('keydown', (e) => {
        handleKeyDown(e)
    })

    //ECOUTE DE L'INACTIVITE
    window.addEventListener("keydown", resetTimer, false);
    startTimer()

    return  player
}