const intialState = []

const monsterReducer = (state = intialState, action) => {
    switch(action.type){
        case "REGISTER_MONSTER":
            return {
                ...state,
                [action.payload.id]: {
                    id:action.payload.id,
                    position:action.payload.position,
                    spriteLocation: '0px 0px',
                    direction: action.payload.direction,
                    walkIndex : 0 
                }
            } 
        case 'MOVE_MONSTER':
            return {
                ...state,
                [action.payload.id]: {
                    id:action.payload.id,
                    position:action.payload.position
                }
            }  
        case 'MOVE_MONSTERS':
            return {
                ...state,
                [action.payload.monster1id]: {
                    id:action.payload.monster1id,
                    position:action.payload.monster1position
                },
                [action.payload.monster2id]: {
                    id:action.payload.monster2id,
                    position:action.payload.monster2position
                },
                [action.payload.monster3id]: {
                    id:action.payload.monster3id,
                    position:action.payload.monster3position
                },
                [action.payload.monster4id]: {
                    id:action.payload.monster4id,
                    position:action.payload.monster4position
                },
                [action.payload.monster5id]: {
                    id:action.payload.monster5id,
                    position:action.payload.monster5position
                },
                [action.payload.monster6id]: {
                    id:action.payload.monster6id,
                    position:action.payload.monster6position
                },
                [action.payload.monster7id]: {
                    id:action.payload.monster7id,
                    position:action.payload.monster7position
                },
                [action.payload.monster8id]: {
                    id:action.payload.monster8id,
                    position:action.payload.monster8position
                },
                [action.payload.monster9id]: {
                    id:action.payload.monster9id,
                    position:action.payload.monster9position
                },
                [action.payload.monster10id]: {
                    id:action.payload.monster10id,
                    position:action.payload.monster10position
                },
                [action.payload.monster11id]: {
                    id:action.payload.monster11id,
                    position:action.payload.monster11position
                },
                [action.payload.monster12id]: {
                    id:action.payload.monster12id,
                    position:action.payload.monster12position
                },
                [action.payload.monster13id]: {
                    id:action.payload.monster13id,
                    position:action.payload.monster13position
                },
            }      
        default:
            return state
    }
}

export default monsterReducer