import { createStore, combineReducers } from 'redux' 
import playerReducer from '../features/player/reducer'
import mapReducer from '../features/map/reducer'
import coinReducer from '../features/coin/reducer'
import monsterReducer from '../features/monster/reducer'
import worldReducer from '../features/world/reducer'
import victoryReducer from '../features/victory/reducer'

const rootReducer = combineReducers({
    player: playerReducer,
    map: mapReducer,
    coins: coinReducer,
    monsters: monsterReducer,
    world: worldReducer,
    victory: victoryReducer,
})

const store = createStore(
    rootReducer,
    //Pour connecter à l'extension Chrome Redux DevTolls
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
)

export default store