const intialState = []

const coinReducer = (state = intialState, action) => {
    switch(action.type){
        case "REGISTER_COIN":
            return {
                ...state,
                [action.payload.id]: {
                    id:action.payload.id,
                    position:action.payload.position,
                    active:true
                }
            }
        case "REMOVE_COIN":
            return {
                ...state,
                [action.payload.id]: {
                    id:action.payload.id,
                    position:action.payload.position,
                    active:false
                }
            }
            // return Object.keys(state)
            //     .filter(key => key !== String(action.payload.id))
            //     .reduce((obj, key) => {
            //         obj[key] = state[key];
            //         return obj;
            //     }, {});
        default:
            return state
    }
}

export default coinReducer