const intialState = {
    position: [0,0],
    spriteLocation: '0px 0px',
    direction: 'EAST',
    walkIndex : 0,
    walkCount: 0,
    lives:5,
    coins:0
}

const playerReducer = (state = intialState, action) => {
    switch(action.type){
        case 'MOVE_PLAYER':
            return {
                ...action.payload
            }
        case 'RESET_PLAYER_POS':
            return {
                ...state,
                position:action.payload.position
            }
        case 'UPDATE_LIVES':
            return {
                ...state,
                lives:action.payload.lives
            }
        case 'UPDATE_COINS':
            return {
                ...state,
                coins:action.payload.coins
            }
        case 'INACTIVITY':
            return {
                ...state,
                spriteLocation:action.payload.spriteLocation
            }
        case 'SLEEP':
            return {
                ...state,
                spriteLocation:action.payload.spriteLocation
            }        
        default:
            return state
    }
}

export default playerReducer