import React from 'react'
import {connect} from 'react-redux'
import Player from '../player'
import Monster from '../monster'
import Coin from '../coin'
import Victory from '../victory'
import Map from '../map'
import {MAP_WIDTH, MAP_HEIGHT, TIME} from '../../config/constants'
import {tiles} from '../../data/maps/1'
import store from '../../config/store'
import nuage1 from './nuage1.png'
import nuage2 from './nuage2.png'
import nuage3 from './nuage3.png'
import nuage4 from './nuage4.png'
import footsteps from '../infoBar/footsteps.png'
import coin from '../infoBar/coin.png'
import start from './start.png'
import heart from '../infoBar/heart.png'
import clock from '../infoBar/clock.gif'
import soundGame from '../../data/sounds/game.mp3'
import soundHurt from '../../data/sounds/hurt.mp3'
import soundGameOver from '../../data/sounds/gameOver.mp3'
import soundVictory from '../../data/sounds/victory.mp3'
import './styles.css'

class World extends React.Component{

    

    componentDidMount(){       
        this.audioGame  = new Audio(soundGame)  
        this.displayStart = 'block'        
    }

    startGame(){
        this.displayStart = 'none' 
        this.audioGame.play()
        this.audioGame.addEventListener('ended', function () {
            this.currentTime = 0;
            this.play();
          }, false);
        var time = TIME        
        this.interval = setInterval(() => {
            //DECREASE DU COMPTEUR TEMPS
            time--
            if(time < 0){
                clearInterval(this.interval)
                store.dispatch({
                    type: 'IS_GAME_OVER',
                    payload: {
                        isGameOver: true
                    }
                })
                //ON REINIT LE PERSO POUR EVITER LES SOUCIS SUR L'ECRAN DE GAME OVER
                store.dispatch({
                    type: 'RESET_PLAYER_POS',
                    payload: {
                        position: [0,0]
                    }
                })  
            }else{
                store.dispatch({
                    type: 'UPDATE_TIME',
                    payload: {
                        time
                    }
                }) 
            }            

            //APPEL AU DEPLACEMENT DES MONSTERS TOUTES LES X SECONDES
            this.monster1.moveFromWorld()
            this.monster2.moveFromWorld()
            this.monster3.moveFromWorld()
            this.monster4.moveFromWorld()
            this.monster5.moveFromWorld()
            this.monster6.moveFromWorld()
            this.monster7.moveFromWorld()
            this.monster8.moveFromWorld()
            this.monster9.moveFromWorld()
            this.monster10.moveFromWorld()
            this.monster11.moveFromWorld()
            this.monster12.moveFromWorld()
            this.monster13.moveFromWorld()
            //DISPATCH DES NOUVELLES POSITIONS DES MONSTERS
            store.dispatch({
                type: 'MOVE_MONSTERS',
                payload: {
                    monster1id:this.monster1.props.id,
                    monster1position: this.monster1.state.position,
                    monster2id:this.monster2.props.id,
                    monster2position: this.monster2.state.position,
                    monster3id:this.monster3.props.id,
                    monster3position: this.monster3.state.position,
                    monster4id:this.monster4.props.id,
                    monster4position: this.monster4.state.position,
                    monster5id:this.monster5.props.id,
                    monster5position: this.monster5.state.position,
                    monster6id:this.monster6.props.id,
                    monster6position: this.monster6.state.position,
                    monster7id:this.monster7.props.id,
                    monster7position: this.monster7.state.position,
                    monster8id:this.monster8.props.id,
                    monster8position: this.monster8.state.position,
                    monster9id:this.monster9.props.id,
                    monster9position: this.monster9.state.position,
                    monster10id:this.monster10.props.id,
                    monster10position: this.monster10.state.position,
                    monster11id:this.monster11.props.id,
                    monster11position: this.monster11.state.position,
                    monster12id:this.monster12.props.id,
                    monster12position: this.monster12.state.position,
                    monster13id:this.monster13.props.id,
                    monster13position: this.monster13.state.position,
                }
            }) 
        }, 500);
    }

    resetGame(){ 
        window.location.reload(false);
    }

    render(){        
        const audioHurt = new Audio(soundHurt)
        const audioGameOver = new Audio(soundGameOver)
        const audioVictory = new Audio(soundVictory)
        audioVictory.volume = 0.5;

        //CREATION DE LA MAP
        store.dispatch({type:'ADD_TILES', payload : {
            tiles:tiles
        }})

        //GESTION DES STYLES EN CAS DE COLLISION AVEC LE CHILD MONSTER
        var collisionAnimation = ''
        var borderColor = '#fff'
        if(this.props.isHurt){
            collisionAnimation = 'shake 0.82s cubic-bezier(.36,.07,.19,.97) both'
            borderColor = 'red'
            audioHurt.play()
        }else{
            collisionAnimation = ''
            borderColor = '#fff'
            audioHurt.pause()
        }

        var displayGameOver = 'none'
        if(this.props.isGameOver){
            displayGameOver = 'flex'
            clearInterval(this.interval)
            this.audioGame.pause()
            audioGameOver.play()
        }else{
            displayGameOver = 'none'
            // audioGame.play()
        }

        var displayVictory = 'none'
        if(this.props.isVictory){
            displayVictory = 'flex'
            clearInterval(this.interval)
            this.audioGame.pause()
            audioVictory.play()
        }else{
            displayVictory = 'none'
        }

        //RESET SI ON MEURS
        if(store.getState().player.lives <= 0){
            store.dispatch({
                type: 'IS_GAME_OVER',
                payload: {
                    isGameOver: true
                }
            })
            //ON REINIT LE PERSO POUR EVITER LES SOUCIS SUR L'ECRAN DE GAME OVER
            store.dispatch({
                type: 'RESET_PLAYER_POS',
                payload: {
                    position: [0,0]
                }
            })         
        }

        // let audio = new Audio(soundfile);
        // audio.play()
        
        return(
            
            <div id="world"                 
                style={{
                    position:'relative',
                    width:`${MAP_WIDTH}px`,
                    height:`${MAP_HEIGHT}px`,
                    margin:'10px auto',
                    animation: collisionAnimation,
                    border: `solid 4px ${borderColor}`,  
                    transform: 'translate3d(0, 0, 0)',
                    backfaceVisibility: 'hidden',
                    perspective: '1000px',
                    overflow:'hidden'           
                }}
            >
                <Map />
                <Coin id="coin1" position={[400,120]} />
                <Coin id="coin2" position={[160,440]} />
                <Coin id="coin3" position={[560,280]} />
                <Coin id="coin4" position={[1040,0]} />
                <Coin id="coin5" position={[1440,80]} />
                <Coin id="coin6" position={[1160,400]} />
                <Coin id="coin7" position={[640,760]} />
                <Coin id="coin8" position={[920,400]} />
                <Coin id="coin9" position={[1320,560]} />
                <Coin id="coin10" position={[480,0]} />
                <Victory position={[920,320]} />
                <Player />
                <Monster onRef={ref => (this.monster1 = ref)} id="monster1" position={[0,360]} direction='EAST' monstreSprite='monster1' />
                <Monster onRef={ref => (this.monster2 = ref)} id="monster2" position={[240,280]} direction='SOUTH' monstreSprite='monster2' />
                <Monster onRef={ref => (this.monster3 = ref)} id="monster3" position={[400,480]} direction='WEST' monstreSprite='monster3' />
                <Monster onRef={ref => (this.monster4 = ref)} id="monster4" position={[480,560]} direction='SOUTH' monstreSprite='monster4' />
                <Monster onRef={ref => (this.monster5 = ref)} id="monster5" position={[480,80]} direction='SOUTH' monstreSprite='monster1' />
                <Monster onRef={ref => (this.monster6 = ref)} id="monster6" position={[480,400]} direction='EAST' monstreSprite='monster2' />
                <Monster onRef={ref => (this.monster7 = ref)} id="monster7" position={[640,320]} direction='WEST' monstreSprite='monster3' />
                <Monster onRef={ref => (this.monster8 = ref)} id="monster8" position={[960,480]} direction='SOUTH' monstreSprite='monster4' />
                <Monster onRef={ref => (this.monster9 = ref)} id="monster9" position={[760,120]} direction='EAST' monstreSprite='monster2' />
                <Monster onRef={ref => (this.monster10 = ref)} id="monster10" position={[1120,440]} direction='EAST' monstreSprite='monster1' />
                <Monster onRef={ref => (this.monster11 = ref)} id="monster11" position={[1240,160]} direction='EAST' monstreSprite='monster4' />
                <Monster onRef={ref => (this.monster12 = ref)} id="monster12" position={[800,760]} direction='EAST' monstreSprite='monster3' />                
                <Monster onRef={ref => (this.monster13 = ref)} id="monster13" position={[880,400]} direction='SOUTH' monstreSprite='monster3' />                
                <img alt="Nuage" style={{position:'absolute',top:'50px',left:'0',opacity:'0.8',animation:'nuage1 80s linear infinite'}} src={nuage1} />
                <img alt="Nuage" style={{position:'absolute',top:'250px',left:'0',opacity:'0.8',animation:'nuage2 400s linear infinite'}} src={nuage2} />
                <img alt="Nuage" style={{position:'absolute',top:'350px',left:'0',opacity:'0.8',animation:'nuage3 100s linear infinite'}} src={nuage3} />
                <img alt="Nuage" style={{position:'absolute',top:'550px',left:'0',opacity:'0.8',animation:'nuage4 150s linear infinite'}} src={nuage4} />
                <div style={{display:displayGameOver, position:'absolute', width:'100%', height:'100%', background:'#000', top:'0', left:'0', color:'#fff', alignContent:'center', alignItems:'center', fontSize:'50px', textAlign:'center'}}>
                    <div style={{width:'100%'}}>
                        Game Over...<br /><br />
                        <div style={{display:'flex', textAlign:'center', width:'100%', alignContent:'center', alignItems:'center'}}><div style={{width:'100%'}}><img alt="Steps" height="40" src={footsteps} style={{marginRight:'5px'}} /> x {store.getState().player.walkCount}</div></div>
                        <div style={{display:'flex', textAlign:'center', width:'100%', alignContent:'center', alignItems:'center'}}><div style={{width:'100%'}}><img alt="Coins" height="40" src={coin} style={{marginRight:'5px'}} /> x {store.getState().player.coins}</div></div><br />
                        <button onClick={() => this.resetGame()}>Play again !</button>
                    </div>                        
                </div>
                <div style={{display:displayVictory, position:'absolute', width:'100%', height:'100%', background:'#000', top:'0', left:'0', color:'#fff', alignContent:'center', alignItems:'center', fontSize:'50px', textAlign:'center'}}>
                    <div style={{width:'100%'}}>
                        VICTORY !!!<br /><br />
                        <div style={{display:'flex', textAlign:'center', width:'100%', alignContent:'center', alignItems:'center'}}><div style={{width:'100%'}}>SCORE : {2*store.getState().world.time + 100*store.getState().player.coins + 400*store.getState().player.lives - 2*store.getState().player.walkCount} pts</div></div>
                        <div style={{display:'flex', textAlign:'center', width:'100%', alignContent:'center', alignItems:'center'}}><div style={{width:'100%'}}><img alt="Steps" height="40" src={footsteps} style={{marginRight:'5px'}} /> x {store.getState().player.walkCount}</div></div>
                        <div style={{display:'flex', textAlign:'center', width:'100%', alignContent:'center', alignItems:'center'}}><div style={{width:'100%'}}><img alt="Coins" height="40" src={coin} style={{marginRight:'5px'}} /> x {store.getState().player.coins}</div></div>
                        <div style={{display:'flex', textAlign:'center', width:'100%', alignContent:'center', alignItems:'center'}}><div style={{width:'100%'}}><img alt="Heart" height="40" src={heart} style={{marginRight:'5px'}} /> x {store.getState().player.lives}</div></div>
                        <div style={{display:'flex', textAlign:'center', width:'100%', alignContent:'center', alignItems:'center'}}><div style={{width:'100%'}}><img alt="Clock" height="40" src={clock} style={{marginRight:'5px'}} /> x {store.getState().world.time}</div></div><br />
                        <button onClick={() => this.resetGame()}>Play again !</button>
                    </div>                        
                </div>
                <div style={{display:this.displayStart, position:'absolute', width:'100%', height:'100%', background:'#000', top:'0', left:'0', color:'#fff', alignContent:'center', alignItems:'center', fontSize:'50px', textAlign:'center'}}>
                    <button style={{background:`url('${start}') no-repeat center center #fff`, cursor:'pointer', marginTop:'350px', width:'350px', height:'70px'}} onClick={() => this.startGame()}></button>
                </div>
            </div>
        )
    }    
}

function mapStateToProps(state){
    return{
        ...state.world
    }
}

export default connect(mapStateToProps)(World)