import React from 'react'
import {connect} from 'react-redux'
import {MAP_WIDTH} from '../../config/constants'
import heart from './heart.png'
import footsteps from './footsteps.png'
import coin from './coin.png'
import clock from './clock.gif'

function InfoBar(props){  
    const lives = []
    for(var i=1; i<=props.player.lives; i++){
        lives.push(<img key={i} alt='Life' height="20" src={heart} />)
    }  

    return(
        <div 
            className="infoBar"
            style={{
                border:'solid 4px #fff' ,
                position:'relative',
                width:`${MAP_WIDTH}px`,
                margin:'10px auto'
            
            }}
        >
            <div
                style={{
                    width:'100%',
                    height:'23px',
                    display:'flex',
                    color:'#fff',
                    padding:'10px',
                    alignItems:'center'            
                }}
            >
                <div style={{position:'absolute',top:'10px',left:'20px',display:'flex',width:'100px'}}><img alt="Steps" height="20" src={footsteps} style={{marginRight:'5px'}} /> x {props.player.walkCount}</div>
                <div style={{position:'absolute',top:'10px',left:'100px', display:'flex',width:'100px'}}><img alt="Coins" height="20" src={coin} style={{marginRight:'5px'}} /> x {props.player.coins}</div>
                <div style={{position:'absolute',top:'12px',left:'200px', display:'flex',width:'120px'}}>
                    {lives}                 
                </div>
                <div style={{position:'absolute',top:'10px',right:'20px', display:'flex'}}><img alt="Timer" height="20" src={clock} style={{marginRight:'5px'}} /> x {props.world.time}</div>
            </div>
            
        </div>
    )
}

function mapStateToProps(state){
    return{
        ...state
    }
}

export default connect(mapStateToProps)(InfoBar)