import React from 'react'
import {connect} from 'react-redux'
import store from '../../config/store'
import victory from './victory.gif'
import {SPRITE_SIZE} from '../../config/constants'

export class Victory extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            position: []
        }
    }

    componentDidMount(){
        this.setState({
            position: this.props.position
        })

        store.dispatch({
            type: 'REGISTER_VICTORY',
            payload: {
                position : this.props.position
            }
        }) 
    }  

    render(){
        return (
            <div
                style={{
                    position:'absolute',
                    top:this.state.position[1],
                    left:this.state.position[0],
                    background: `url('${victory}') no-repeat center center transparent`,
                    backgroundSize:'120%',
                    backgroundPosition: 'center center',
                    width:`${SPRITE_SIZE}px`,
                    height:`${SPRITE_SIZE}px`
                }}
            />
        )
    }
}

function mapStateToProps(state){
    return{
        ...state.victory
    }
}

export default connect(mapStateToProps)(Victory)