import React from 'react'
import {connect} from 'react-redux'
import store from '../../config/store'
import {SPRITE_SIZE, MAP_HEIGHT, MAP_WIDTH} from '../../config/constants'

export class Monster extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            position: [0,0],
            spriteLocation: '0px 0px',
            direction: 'EAST',
            walkIndex : 0 
        }
    }

    componentDidMount(){
        this.props.onRef(this)

        this.setState({
            position: this.props.position,
            spriteLocation: '0px 0px',
            direction: this.props.direction,
            walkIndex : 0          
        })

        store.dispatch({
            type: 'REGISTER_MONSTER',
            payload: {
                id: this.props.id,
                position: this.props.position,
                spriteLocation: '0px 0px',
                direction: this.props.direction,
                walkIndex : 0 
            }
        }) 
    }

    //DETECT COLLISION WITH PLAYER
    detectCollision(playerPos, monsterPos) {
        if (playerPos[0] === monsterPos[0] && playerPos[1] === monsterPos[1]) {
            return true;
        }else{
            return false;
        }
        
    }

    //DEPLACEMENT AVEC SETINTERVAL DEPUIS WORLD
    moveFromWorld(){
        this.handleMove(this.state.direction)
        // console.log('ok')
    }

    //FONCTION APPELLEE AU CLICK SUR UNE FLECHE DU CLAVIER
    handleMove(direction){
        switch(direction){
            case 'WEST':
                return this.attemptMove('WEST')
            case 'EAST':
                return this.attemptMove('EAST')
            case 'NORTH':
                return this.attemptMove('NORTH')
            case 'SOUTH':
                return this.attemptMove('SOUTH')
            default:
        }
    }

    //QUAND L'UTILISATEUR ESSAYE DE FAIRE UN DEPLACEMENT
    attemptMove(direction){
        const oldPos = this.state.position
        const newPos = this.getNewPosition(oldPos,direction)
        //SI LA NOUVELLE POSITION N'EST PAS EN DEHORS DES LIMITES ET N'EST PAS UN OBJET NON TRAVERSABLE
        if(this.observeBoundaries(newPos) && this.observeImpassable(newPos)){
            this.dispatchMove(direction, newPos)            
        }else{
            switch(direction){
                case 'WEST':
                    return this.attemptMove('EAST')
                case 'EAST':
                    return this.attemptMove('WEST')
                case 'NORTH':
                    return this.attemptMove('SOUTH')
                case 'SOUTH':
                    return this.attemptMove('NORTH')
                default:
            }
        }
    }

    //UPDATE DE LA POSITION
    getNewPosition(oldPos, direction){
        switch(direction){
            case 'WEST':
                return [oldPos[0]-SPRITE_SIZE, oldPos[1]]
            case 'EAST':
                return [oldPos[0]+SPRITE_SIZE, oldPos[1]]
            case 'NORTH':
                return [oldPos[0], oldPos[1]-SPRITE_SIZE]
            case 'SOUTH':
                return [oldPos[0], oldPos[1]+SPRITE_SIZE]
            default:
        }        
    }

    //SI ON EST DANS LA MAP ON RETOURNE TRUE SINON FALSE
    observeBoundaries(newPos){        
        return (newPos[0] >= 0 && newPos[0] <= MAP_WIDTH-SPRITE_SIZE) &&
                (newPos[1] >= 0 && newPos[1] <= MAP_HEIGHT-SPRITE_SIZE)
    }

    //TEST DES OBSTACLES
    observeImpassable(newPos){
        const tiles = store.getState().map.tiles
        const y = newPos[1] / SPRITE_SIZE
        const x = newPos[0] / SPRITE_SIZE
        const nextTile = tiles[y][x]
        return  nextTile < 5
    }

    //FONCTION QUI FAIT BOUCLER LE WAKL INDEX UNE FOIS QU'ON EST AU BOUT
    getWalkIndex(){
        const walkIndex = this.state.walkIndex
        return walkIndex >= 4 ? 0 : walkIndex+1
    }

    //ON AFFICHE LE BON SPRITE POUR L'ANIMATION DU DEPLACEMENT
    getSpriteLocation(direction, walkIndex){
        switch (direction){
            case 'EAST' :
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*2}px`
            case 'SOUTH' :
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*0}px`
            case 'WEST' :
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*3}px`
            case 'NORTH' :
                return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*1}px`
            default:
            
        }
    }

    //DISPATCH DE LA NOUVELLE POSITION ET DIRECTION POUR ANIMATION DU MONSTRE
    dispatchMove(direction, newPos){
        const walkIndex = this.getWalkIndex()
        this.setState({
            position: newPos,
            direction,
            walkIndex,
            spriteLocation : this.getSpriteLocation(direction, walkIndex)            
        })

        //TEST DE LA COLLISION AVEC LE PLAYER
        if(this.detectCollision(store.getState().player.position, newPos)){   
            //UPDATE DU STORE POUR ANIMATION PLAYER TOUCHE
            store.dispatch({
                type: 'IS_HURT',
                payload: {
                    isHurt:true
                }
            }) 
            setTimeout(() => {
                //PUIS REINITIALISER APRES X SECONDES
                store.dispatch({
                    type: 'IS_HURT',
                    payload: {
                        isHurt:false
                    }
                }) 
            }, 300); 
            
            //ON ENLEVE UNE VIE
            const lives = store.getState().player.lives - 1
            //ON DISPATCHE AU STORE AVEC LA VIE EN MOINS
            store.dispatch({
                type: 'UPDATE_LIVES',
                payload: {
                    lives
                }
            })        
        }
    }

    render(){
        const monsterSprite = require(`./${this.props.monstreSprite}.png`)
        return (
            <div
                style={{
                    position:'absolute',
                    top:this.state.position[1],
                    left:this.state.position[0],
                    backgroundImage: `url('${monsterSprite}')`,
                    backgroundPosition: this.state.spriteLocation,
                    width:`${SPRITE_SIZE}px`,
                    height:`${SPRITE_SIZE}px`
                }}
            />
        )
    }
}

export default connect()(Monster)